import { isTheSameAddress } from "../helpers";
import useStrategies from "./useStrategies";

export default function useStrategyWithoutData(strategyAddress) {
  const { data: st, isLoading } = useStrategies();
  return {
    data: st?.find((st) => isTheSameAddress(st.id, strategyAddress)),
    isLoading: isLoading,
  };
}
