import { useCallback, useState } from "react";
import { usePromiseTransaction } from "@usedapp/core/dist/esm/src/hooks/usePromiseTransaction";
import { useEthers } from "@usedapp/core";

export function connectContractToSigner(contract, options, library) {
  if (contract.signer) {
    return contract;
  }

  if (options?.signer) {
    return contract.connect(options.signer);
  }

  if (library?.getSigner()) {
    return contract.connect(library.getSigner());
  }

  throw new TypeError("No signer available in contract, options or library");
}

export function useContractBatchFunction(contract, options = {}) {
  const { library, chainId } = useEthers();
  const { promiseTransaction, state } = usePromiseTransaction(chainId, options);
  const [events, setEvents] = useState(undefined);

  const send = useCallback(
    async (args, args1, ops = {}) => {
      // console.log("useContractBatchFunction args", args, args1);
      // console.log(
      //   "useContractBatchFunction args map",
      //   (await args).map((t) => t.data)
      // );
      const contractWithSigner = connectContractToSigner(
        contract,
        options,
        library
      );
      const receipt = await promiseTransaction(
        contractWithSigner.batch(
          args.map((t) => t.data),
          args1,
          ops
        )
      );
      if (receipt?.logs) {
        console.log("useContractBatchFunction logs", receipt?.logs);
        setEvents(events);
      }
    },
    // eslint-disable-next-line
    [contract, options, library, promiseTransaction]
  );

  return { send, state, events };
}
