import { useEthers } from "@usedapp/core";
import { useCallback, useEffect, useState } from "react";
import useDebounce from "../hooks/useDebounce";
import { useApplicationDispatch } from "./appContext";

export default function BlockUpdater() {
  const { library, chainId } = useEthers();
  const { updateBlockNumber } = useApplicationDispatch();

  const [state, setState] = useState({
    blockNumber: null,
  });

  const blockNumberCallback = useCallback(
    (blockNumber) => {
      console.log("New block: ", blockNumber);
      setState({ blockNumber: blockNumber });
    },
    [chainId, setState] // eslint-disable-line
  );

  // TODO: replace with useEthers
  useEffect(() => {
    if (!library) return undefined;

    setState({ blockNumber: null });

    library
      .getBlockNumber()
      .then(blockNumberCallback)
      .catch((error) =>
        console.error(
          `Failed to get block number for chainId: ${chainId}`,
          error
        )
      );

    library.on("block", blockNumberCallback);
    return () => {
      library.removeListener("block", blockNumberCallback);
    };
  }, [chainId, library, blockNumberCallback]);

  const debouncedState = useDebounce(state, 100);

  useEffect(() => {
    if (!debouncedState.blockNumber) return;
    updateBlockNumber({ blockNumber: debouncedState.blockNumber });
  }, [debouncedState.blockNumber]); // eslint-disable-line

  return null;
}
