import { useEthers } from "@usedapp/core";
import { useCallback, useEffect, useState } from "react";
import { isMobile } from "react-device-detect";
import { injected } from "../connectors";
import {
  APPLICATION_MODAL,
  useApplicationDispatch,
  useApplicationState,
} from "../context/appContext";

export function useEagerConnect() {
  const { activate, active } = useEthers(); // specifically using useWeb3ReactCore because of what this hook does
  const [tried, setTried] = useState(false);

  useEffect(() => {
    injected.isAuthorized().then((isAuthorized) => {
      if (isAuthorized) {
        activate(injected, undefined, true).catch(() => {
          setTried(true);
        });
      } else {
        if (isMobile && window.ethereum) {
          activate(injected, undefined, true).catch(() => {
            setTried(true);
          });
        } else {
          setTried(true);
        }
      }
    });
  }, [activate]); // intentionally only running on mount (make sure it's only mounted once :))

  // if the connection worked, wait until we get confirmation of that to flip the flag
  useEffect(() => {
    if (active) {
      setTried(true);
    }
  }, [active]);

  return tried;
}

/**
 * Use for network and injected - logs user in
 * and out after checking what network theyre on
 */
export function useInactiveListener(suppress = false) {
  const { active, error, activate } = useEthers(); // specifically using useWeb3React because of what this hook does

  useEffect(() => {
    const { ethereum } = window;

    if (ethereum && ethereum.on && !active && !error && !suppress) {
      const handleChainChanged = () => {
        // eat errors
        activate(injected, undefined, true).catch((error) => {
          console.error("Failed to activate after chain changed", error);
        });
      };

      const handleAccountsChanged = (accounts) => {
        if (accounts.length > 0) {
          // eat errors
          activate(injected, undefined, true).catch((error) => {
            console.error("Failed to activate after accounts changed", error);
          });
        }
      };

      ethereum.on("chainChanged", handleChainChanged);
      ethereum.on("accountsChanged", handleAccountsChanged);

      return () => {
        if (ethereum.removeListener) {
          ethereum.removeListener("chainChanged", handleChainChanged);
          ethereum.removeListener("accountsChanged", handleAccountsChanged);
        }
      };
    }
    return undefined;
  }, [active, error, suppress, activate]);
}

export function useModalOpen(modal) {
  const { openModal } = useApplicationState();
  return openModal === modal;
}

export function useModalParams() {
  const { modalParams } = useApplicationState();
  return modalParams;
}

export function useToggleModal(modal, params = null) {
  const open = useModalOpen(modal);
  const { setOpenModal } = useApplicationDispatch();
  return useCallback(
    () => setOpenModal(open ? null : modal, params),
    [modal, open] // eslint-disable-line
  );
}

export function useWalletModalToggle(params = null) {
  return useToggleModal(APPLICATION_MODAL.WALLET, params);
}

export function useJoinStrategyModalToggle(params = null) {
  return useToggleModal(APPLICATION_MODAL.JOIN_TO_STRATEGY, params);
}

export function useExitStrategyModalToggle(params = null) {
  return useToggleModal(APPLICATION_MODAL.EXIT_FROM_STRATEGY, params);
}

export function useWithdrawFromVaultModalToggle(params = null) {
  return useToggleModal(APPLICATION_MODAL.WITHDRAW_FROM_VAULT, params);
}

export function useTransferToAgreementModalToggle(params = null) {
  return useToggleModal(APPLICATION_MODAL.TRANSFER_TO_AGREEMENT, params);
}

export function useBlockNumber() {
  const { blockNumber } = useApplicationState();

  return blockNumber;
}
