import { isTheSameAddress } from "../helpers";
import useStrategiesFromAccount from "./useStrategiesFromAccount";

export default function useStrategyAgreementHoldings(strategyAddress) {
  const { data: accountData, isLoading } = useStrategiesFromAccount();

  return {
    data: accountData?.strategies?.find((st) =>
      isTheSameAddress(st.strategy.id, strategyAddress)
    ),
    isLoading: isLoading,
  };
}
