import {
  Text,
  Box,
  Flex,
  Heading,
  SkeletonCircle,
  SkeletonText,
  Skeleton,
  Image,
} from "@chakra-ui/react";
import { InstrumentsLogos } from "../../assets/Instruments";
import { MimicBadge } from "./MimicBadge";
import { useTranslation } from "react-i18next";
import useMetadata from "../../hooks/useMetadata";
import ReactMarkdown from "react-markdown";
import { ChainId } from "@usedapp/core";
import { getAssetImg } from "../../helpers";
import { useMemo } from "react";
import { useAllTokens } from "../../hooks/useAllTokens";
import useToken from "../../hooks/useToken";

export const StrategyTitle = ({ stId, tokenIn, hideDescription }) => {
  const { data: meta, isLoading } = useMetadata(stId);
  const allTokens = useAllTokens();
  const tokenInfo = useToken(tokenIn);
  const description = getCompleteDescription(
    meta?.description,
    tokenInfo,
    allTokens
  );
  return (
    <>
      {!isLoading ? (
        <Flex
          minH={!hideDescription ? "210px" : "0"}
          direction="column"
          h="full"
        >
          <Flex
            direction="column"
            align="flex-start"
            h="full"
            fontSize="0.9rem"
          >
            <Flex
              justify="space-between"
              color="gray"
              w="full"
              py="2"
              mb="4"
              bg={`${tokenInfo.bg}26`}
              px="6"
            >
              <Text>BASE TOKEN</Text>
              <Flex>
                <Text>{tokenInfo.name}</Text>

                <Image
                  ml="2"
                  h="23px"
                  src={getAssetImg(tokenInfo[ChainId.Mainnet])}
                />
              </Flex>
            </Flex>
            <Flex px="6" align="flex-start" justify="space-between" w="full">
              <Flex wrap="wrap" h="full">
                {meta?.protocols &&
                  meta?.protocols.map((inst) => {
                    return (
                      <Image
                        key={inst}
                        mr="1"
                        mb="2"
                        h="35px"
                        src={InstrumentsLogos[inst]}
                      />
                    );
                  })}
              </Flex>
              <RenderBadge type={meta?.type} oneColor={hideDescription} />
            </Flex>
            <Flex align="center" pt="2" px="6">
              <Text fontSize="1rem" pr="1">
                {meta ? `v${meta?.version}` : "na"}
              </Text>
              <Text
                textTransform="uppercase"
                fontSize="1.25rem"
                fontWeight="600"
                noOfLines={1}
              >
                {meta?.title}
              </Text>
            </Flex>

            {!hideDescription && (
              <>
                <Flex w="full" align="center" px="6">
                  <Text fontSize="1rem" noOfLines={4}>
                    <Flex>
                      <RenderMarkdown>{description}</RenderMarkdown>
                    </Flex>
                  </Text>
                </Flex>
              </>
            )}
          </Flex>
        </Flex>
      ) : (
        <LoadingSkeleton hideDescription={hideDescription} />
      )}
    </>
  );
};

const RenderBadge = ({ type, oneColor }) => {
  const { t } = useTranslation();
  const isStable = type?.toUpperCase() === "STABLE";
  return (
    <MimicBadge schema={oneColor ? "subtle" : isStable ? "light" : null} mt="1">
      {isStable ? t("stableStrategy") : t("tokenStrategy")}
    </MimicBadge>
  );
};

const LoadingSkeleton = ({ hideDescription }) => {
  return (
    <Box minH={!hideDescription ? "210px" : "0"} py="6" px="6">
      <Flex justify="space-between" w="full" align="center">
        <Flex>
          <SkeletonCircle size="12" />
          <SkeletonCircle size="12" ml="-3" />
        </Flex>
        <Skeleton h="25px" w="130px" />
      </Flex>
      <Skeleton mt="6">
        <Heading textTransform="uppercase" fontSize="1.5rem" noOfLines={1}>
          the hidden title skeleton
        </Heading>
      </Skeleton>
      <SkeletonText mt="4" noOfLines={3} spacing="4" />
    </Box>
  );
};

const RenderMarkdown = ({ children }) => {
  return useMemo(() => {
    return (
      <ReactMarkdown
        children={children}
        components={{
          img: (props) => (
            <Image {...props} h="12px" src={props.src} d="inline" />
          ),
        }}
      />
    );
  }, [children]);
};

const getCompleteDescription = (description, token, allTokens) => {
  // complex markdown situation
  const a = description?.replaceAll(
    "{{token}}",
    `![${token?.name}](${getAssetImg(
      token[ChainId.Mainnet]
    )}) ${token?.name?.toUpperCase()}`
  );

  const b = allTokens?.reduce(
    (accum, t) =>
      accum?.replace(
        `{{${t?.name?.toLowerCase()}}}`,
        `![${t?.name}](${getAssetImg(
          t[ChainId.Mainnet]
        )}) ${t?.name?.toUpperCase()}`
      ),
    a
  );

  return b;
};
