import { useMemo } from "react";
import useSWR from "swr";
import axios from "axios";
import { useEthers } from "@usedapp/core";
import { isAddress } from "../helpers";

export const fetcher = (url) =>
  url ? axios.get(url).then((res) => res.data) : null;

export default function useApy(strategyId) {
  const { chainId } = useEthers();

  const url = `${process.env.REACT_APP_APY_URL}/${chainId}/${isAddress(
    strategyId
  )}`;
  const { data, error } = useSWR(url, fetcher);

  return useMemo(() => {
    // console.log("useMetadata ", data, error);
    return {
      title: data?.fields?.title?.stringValue,
      apy: data?.fields?.value?.stringValue,
      isLoading: !error && !data,
      isError: error,
    };
  }, [data, error]);
}
